// NavBar.js -   
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { history } from "../redux/configureStore";

//  + 
import addBtn from "../image/addBtn.png";
import plus from "../image/plus.png";
import Top from "../image/top.png";
import HomeIcon from "../image/NavBar_Home.png";
import WalkIcon from "../image/NavBar_Walk.png";
import DogstagramIcon from "../image/NavBar_Dogstagram.png";
import MyPageIcon from "../image/NavBar_MyPage.png";

const NavBar = (props) => {
  const { add_dogsta } = props;

  const userId = localStorage.getItem("userId");
  const dog = localStorage.getItem("checkDog");
  const [page, setPage] = useState();

  // top 
  const [btnStatus, setBtnStatus] = useState(false);
  const [scrollY, setScrollY] = useState(0);

  //  200px   top  
  const handleFollow = () => {
    setScrollY(window.pageYOffset);
    if (scrollY > 200) {
      setBtnStatus(true);
    } else {
      setBtnStatus(false);
    }
  };

  useEffect(() => {
    const watch = () => {
      window.addEventListener("scroll", handleFollow);
    };
    watch();
    return () => {
      window.removeEventListener("scroll", handleFollow);
    };
  });

  // top     
  const handleTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setScrollY(0);
    setBtnStatus(false);
  };

  // ,          
  const postHandler = () => {
    if (dog == "false") {
      window.alert("  ");
      history.push("/signDog");
      return;
    }
    history.push("/registerwalk");
  };

  const dogStarHandler = () => {
    if (dog == "false") {
      window.alert("  ");
      history.push("/signDog");
      return;
    }
    history.push("/dogStaWrite");
  };

  //   ,    
  const myPageHandler = () => {
    if (!userId) {
      //     
      window.alert("  ");
      history.push("/login");
      return;
    } else if (dog == "false") {
      //       
      window.alert("  ");
      history.push("/signDog");
      return;
    }
    // ,      
    history.push(`/mypage/${userId}`);
  };

  if (add_dogsta) {
    //   navbar
    return (
      <div>
        <Nav>
          <Box>
            {/*   */}
            <Button
              onClick={() => history.push("/")}
              style={{ marginRight: "20px" }}
            >
              <img src={HomeIcon} style={{ width: "20px", height: "20px" }} />
              <Text></Text>
            </Button>

            {/*    */}
            <Button
              onFocus={() => {
                setPage("all");
              }}
              onClick={() => {
                history.push("/alllist/all");
              }}
              style={{ marginRight: "92px" }}
            >
              <img src={WalkIcon} style={{ width: "20px", height: "20px" }} />
              <Text></Text>
            </Button>

            {/*   */}
            <Button
              onClick={() => {
                history.push("/dogStaMain");
              }}
              style={{ marginRight: "20px" }}
            >
              <img
                src={DogstagramIcon}
                style={{ width: "20px", height: "20px" }}
              />
              <Text></Text>
            </Button>

            {/*   */}
            <Button onClick={myPageHandler}>
              <img src={MyPageIcon} style={{ width: "20px", height: "20px" }} />
              <Text>My</Text>
            </Button>
          </Box>

          {/*    */}
          <HomeArea>
            <HomeBtn onClick={dogStarHandler}>
              <img
                src={plus}
                style={{ display: "block", width: "58px", height: "58px" }}
              />
            </HomeBtn>
            <UploadText></UploadText>
          </HomeArea>

          {/* top  */}
          {btnStatus ? (
            <TopWrap onClick={handleTop}>
              <TopBtn>
                <img src={Top} />
                <p>TOP</p>
              </TopBtn>
            </TopWrap>
          ) : null}
        </Nav>
      </div>
    );
  } else {
    //  navbar
    return (
      <div>
        <Nav>
          <Box>
            {/*   */}
            <Button
              onClick={() => history.push("/")}
              style={{ marginRight: "8px" }}
            >
              <img src={HomeIcon} style={{ width: "20px", height: "20px" }} />
              <Text></Text>
            </Button>

            {/*    */}
            <Button
              onFocus={() => {
                setPage("all");
              }}
              onClick={() => {
                history.push(`/alllist/${page}`);
              }}
              style={{ marginRight: "92px" }}
            >
              <img src={WalkIcon} style={{ width: "20px", height: "20px" }} />
              <Text></Text>
            </Button>

            {/*   */}
            <Button
              onClick={() => {
                history.push("/dogStaMain");
              }}
              style={{ marginRight: "8px" }}
            >
              <img
                src={DogstagramIcon}
                style={{ width: "20px", height: "20px" }}
              />
              <Text></Text>
            </Button>

            {/*   */}
            <Button onClick={myPageHandler}>
              <img src={MyPageIcon} style={{ width: "20px", height: "20px" }} />
              <Text>My</Text>
            </Button>
          </Box>

          {/*    */}
          <HomeArea>
            <HomeBtn onClick={postHandler}>
              <img
                src={addBtn}
                style={{ display: "block", width: "100%", height: "100%" }}
              />
            </HomeBtn>
            <Walk></Walk>
          </HomeArea>

          {/* top  */}
          {btnStatus ? (
            <TopWrap onClick={handleTop}>
              <TopBtn>
                <img src={Top} />
                <p>TOP</p>
              </TopBtn>
            </TopWrap>
          ) : null}
        </Nav>
      </div>
    );
  }
};

NavBar.defaultProps = {
  add_dogsta: false,
};
const Walk = styled.div`
  position: absolute;
  bottom: 17.7px;
  left: 30%;
  font-size: 11px;
`;
const HomeArea = styled.div`
  border: none;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  font-size: 12px;
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: -26px;
  transform: translateX(-50%);
`;
const Nav = styled.div`
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  min-width: 315px;
  max-width: 500px;
  width: 100%;
  height: 68px;
  z-index: 5;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.15);
`;
const Box = styled.div`
  box-sizing: border-box;
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 68px;
  padding: 6px 30px;
  background-color: #fff;
`;
const Button = styled.button`
  background-color: transparent;
  border: none;
  cursor: pointer;
`;
const HomeBtn = styled.button`
  border: none;

  box-sizing: border-box;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  left: 50%;
  background-color: transparent;

  transform: translateX(-50%);
`;

const Text = styled.div`
  font-size: 12px;
  padding-top: 6px;
`;
const UploadText = styled.div`
  position: absolute;
  bottom: 17.7px;
  left: 36%;
  font-size: 11px;
`;

const TopWrap = styled.div`
  position: absolute;
  top: -50px;
  right: 0;
`;
const TopBtn = styled.button`
  width: 50px;
  height: 44px;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 2px 10px 0 4px;
  cursor: pointer;
  p {
    font-size: 12px;
    font-weight: 600;
  }
`;
export default NavBar;
