const addPostMD = (formData) => {
  return function (dispatch, getState, { history }) {
    axios({
      method: "POST",
      url: "http://13.209.70.209/dogsta/write",
      data: formData,
      headers: {
        "Content-Type": "multipart/form-data; ",
        accept: "application/json",
        "Access-Control-Allow-Origin": "*",
        authorization: `Bearer ${getCookie("token")}`,
      },
    })
      .then((res) => {
        dispatch(getFirstRecentMD());
        dispatch(modalActions.setModal(" !"));
        history.push("/dogstarmainmodal");
        // console.log("  ", res);
      })
      .catch((err) => {
        // console.log("  POST ", err);
      });
  };
};

const editPostMD = (postId, post) => {
  return function (dispatch, useState, { history }) {
    axios({
      method: "PATCH",
      url: `http://13.209.70.209/dogsta/${postId}`,
      data: post,
      headers: {
        accept: "application/json",
        "Access-Control-Allow-Origin": "*",
        authorization: `Bearer ${getCookie("token")}`,
      },
    })
      .then((res) => {
        dispatch(editPost(post));
        dispatch(getModal(true));
        // console.log("  PATCH ", res);
      })
      .catch((err) => {
        // console.log("  PATCH ", err);
      });
  };
};

